<template>
    <div :class="{'ly-is-full':isFull}">
        <div class="tableSelect" ref="tableSelect">
            <el-form :inline="true" :model="formInline" label-position="left">
                <el-form-item label="">
                    <el-input v-model.trim="formInline.nickname" maxlength="60"  clearable placeholder="" @change="search" style="width:200px"></el-input>
<!--                </el-form-item>-->
<!--                                <el-form-item label="">-->
<!--                                    <el-date-picker-->
<!--                                            style="width:350px"-->
<!--                                            v-model="timers"-->
<!--                                            size="small"-->
<!--                                            type="datetimerange"-->
<!--                                            @change="timeChange"-->
<!--                                            range-separator=""-->
<!--                                            start-placeholder=""-->
<!--                                            end-placeholder="">-->
<!--                                    </el-date-picker>-->
                  </el-form-item>
                <el-form-item label="">
                    <el-input  v-model.trim="formInline.mobile" maxlength="60"  clearable placeholder="" @change="search" style="width:200px"></el-input>
                  </el-form-item>
                <el-form-item label="">
                    <el-date-picker
                            style="width:350px"
                            v-model="timers"
                            type="datetimerange"
                            @change="timeChange"
                            range-separator=""
                            start-placeholder=""
                            end-placeholder="">
                    </el-date-picker>
                  </el-form-item>
                <el-form-item label=""><el-button  @click="search" type="primary" icon="Search" v-show="isShowBtn('userFeekback','','Search')"></el-button></el-form-item>
                <el-form-item label=""><el-button  @click="handleEdit('','reset')" icon="Refresh"></el-button></el-form-item>
                <el-form-item label=""><el-button  @click="handleDelete" type="danger" :disabled="multiple" v-show="isShowBtn('userFeekback','','Delete')"></el-button></el-form-item>
            </el-form>
        </div>

        <div class="table">
            <el-table :height="tableHeight"  border :data="tableData" v-loading="loadingPage" ref="tableref" style="width: 100%" tooltip-effect="dark" @selection-change="handleSelectionChange">
                <el-table-column type="selection" width="60" align="center"></el-table-column>
                <el-table-column type="index" width="60" align="center" label="">
                    <template #default="scope">
                        <span v-text="getIndex(scope.$index)"></span>
                    </template>
                </el-table-column>
<!--                <el-table-column type="selection" width="55" align="center"></el-table-column>-->
                <el-table-column min-width="70" prop="avatar" label="">
                    <template #default="scope">
                        <img  :src="scope.row.avatar ? scope.row.avatar : defaultImg" style="width: 30px;height: 30px" :onerror="defaultImg">
                    </template>
                </el-table-column>
                <el-table-column min-width="100" prop="nickname" label=""></el-table-column>
                <el-table-column min-width="100" prop="mobile" label=""></el-table-column>
                <el-table-column min-width="200" prop="message" label="" show-overflow-tooltip=""></el-table-column>
<!--                <el-table-column min-width="100" label="">-->
<!--                    <template slot-scope="scope">-->
<!--                        <el-tag v-if="scope.row.status"></el-tag>-->
<!--                        <el-tag v-else type="danger"></el-tag>-->
<!--                    </template>-->
<!--                </el-table-column>-->
                <el-table-column min-width="150" prop="create_datetime" label=""></el-table-column>
                <el-table-column label="" width="100">
                    <template #header>
                        <div style="display: flex;justify-content: space-between;align-items: center;">
                            <div></div>
                            <div @click="setFull">
                                <el-tooltip content="" placement="bottom">
                                    <el-icon ><full-screen /></el-icon>
                                </el-tooltip>
                            </div>
                        </div>
                    </template>
                    <template #default="scope">
<!--                        v-show="isShowBtn('goodClass','','Delete')"-->
                        <span class="table-operate-btn" @click="handleEdit(scope.row,'detail')" v-show="isShowBtn('userFeekback','','Retrieve')"></span>
                        <span class="table-operate-btn" @click="handleEdit(scope.row,'delete')" v-show="isShowBtn('userFeekback','','Delete')"></span>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <Pagination v-bind:child-msg="pageparm" @callFather="callFather"></Pagination>
        <add-module ref="addModuleFlag"></add-module>
    </div>
</template>
<script>
    import Pagination from "@/components/Pagination";
    import {dateFormats} from "@/utils/util";
    import {PlatformsettingsUserfeeckback,PlatformsettingsUserfeeckbackDelete} from '@/api/api'
    import AddModule from "./components/addModule";
    import { lyMixins } from "@/mixins/mixins"
    export default {
        mixins: [lyMixins],
        components:{
            AddModule,
            Pagination,
        },
        name:'userFeekback',
        data() {
            return {
                isFull:false,
                loadingPage:false,
                // 
                ids: [],
                // 
                single: true,
                // 
                multiple: true,
                defaultImg:require('../../assets/img/avatar.jpg'),
                formInline:{
                    page: 1,
                    limit: 10,
                },
                pageparm: {
                    page: 1,
                    limit: 10,
                    total: 0
                },
                statusList:[
                    {id:1,name:''},
                    {id:0,name:''}
                ],
                timers:[],
                tableData:[]
            }
        },
        methods:{
            setFull(){
                this.isFull=!this.isFull
                window.dispatchEvent(new Event('resize'))
            },
            // 
            getIndex($index) {
                // ( - 1) *  +  + 1
                return (this.pageparm.page-1)*this.pageparm.limit + $index +1
            },
            //
            handleSelectionChange(selection) {
                this.ids = selection.map(item => item.id);
                this.single = selection.length !== 1;
                this.multiple = !selection.length;
            },
            /**  */
            handleDelete(row) {
                const ids = this.ids;
                let vm = this
                vm.$confirm('?', "", {
                    confirmButtonText: "",
                    cancelButtonText: "",
                    type: "warning"
                }).then(function() {
                    return PlatformsettingsUserfeeckbackDelete({id:ids}).then(res=>{
                        if(res.code == 2000) {
                            vm.$message.success(res.msg)
                            vm.search()
                        } else {
                            vm.$message.warning(res.msg)
                        }
                    })
                })
            },
            // addAdmin() {
            //     this.$refs.addGoodClassModuleFlag.addGoodClassModuleFn(null,'')
            // },
            handleEdit(row,flag) {
                if(flag=='detail') {
                    this.$refs.addModuleFlag.addModuleFn(row,'')
                }
                else if(flag=='delete') {
                    let vm = this
                    vm.$confirm('',{
                        closeOnClickModal:false
                    }).then(res=>{
                        PlatformsettingsUserfeeckbackDelete({id:row.id}).then(res=>{
                            if(res.code == 2000) {
                                vm.$message.success(res.msg)
                                vm.search()
                            } else {
                                vm.$message.warning(res.msg)
                            }
                        })
                    }).catch(()=>{

                    })
                }
                else if(flag=="reset"){
                    this.formInline = {
                        page:1,
                        limit: 10
                    }
                    this.pageparm={
                        page: 1,
                        limit: 10,
                        total: 0
                    }
                    this.timers = []
                    this.getData()
                }
            },

            callFather(parm) {
                this.formInline.page = parm.page
                this.formInline.limit = parm.limit
                this.getData()
            },
            search() {
                this.formInline.page = 1
                this.formInline.limit = 10
                this.getData()
            },
            //
            async getData() {
                this.loadingPage = true
                PlatformsettingsUserfeeckback(this.formInline).then(res => {
                     this.loadingPage = false
                     if(res.code ==2000) {
                         this.tableData = res.data.data
                         this.pageparm.page = res.data.page;
                         this.pageparm.limit = res.data.limit;
                         this.pageparm.total = res.data.total;
                     }
                 })
            },

            timeChange(val){
                if (val) {
                    this.formInline.beginAt=dateFormats(val[0],'yyyy-MM-dd hh:mm:ss');
                    this.formInline.endAt=dateFormats(val[1],'yyyy-MM-dd hh:mm:ss');
                } else {
                    this.formInline.beginAt = null
                    this.formInline.endAt = null
                }
                this.search()
            },
        },
        created() {
            this.getData()
        },
        timers(val){
            if (val) {
                this.formInline.beginAt=dateFormats(val[0],'yyyy-MM-dd hh:mm:ss');
                this.formInline.endAt=dateFormats(val[1],'yyyy-MM-dd hh:mm:ss');
            } else {
                this.formInline.beginAt = ''
                this.formInline.endAt = ''
            }
            this.getData()
        },
    }
</script>
