////
// import * as echarts from 'echarts';

//  echarts  echarts 
import * as echarts from "echarts/core";

//
/** and Chart  */
import { BarChart, LineChart, RadarChart,PieChart } from "echarts/charts";

//  Component
import {
    LegendComponent,
    ToolboxComponent,
    TitleComponent,
    TooltipComponent,
    GridComponent,
    DatasetComponent,
    TransformComponent,
} from "echarts/components";

// 
import { LabelLayout, UniversalTransition } from "echarts/features";

//  Canvas  CanvasRenderer  SVGRenderer 
import { CanvasRenderer } from "echarts/renderers";

// 
echarts.use([
    LegendComponent,
    ToolboxComponent,
    TitleComponent,
    TooltipComponent,
    GridComponent,
    DatasetComponent,
    TransformComponent,
    BarChart,
    LabelLayout,
    UniversalTransition,
    CanvasRenderer,
    LineChart,
    PieChart,
    RadarChart,
]);

// 
export default echarts;