<template>
    <div :class="{'ly-is-full':isFull}">
        <div class="tableSelect" ref="tableSelect">
            <el-form :inline="true" :model="formInline" label-position="left">
                <el-form-item label="">
                    <el-input size="default" v-model.trim="formInline.name" maxlength="60" clearable placeholder=""
                              @change="search" style="width:200px"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input size="default" v-model.trim="formInline.nianxian" maxlength="60" clearable
                              placeholder="" @change="search" style="width:200px"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input size="default" v-model.trim="formInline.xueli" maxlength="60" clearable placeholder=""
                              @change="search" style="width:200px"></el-input>
                </el-form-item>
                <!--                <el-form-item label="">-->
                <!--                    <el-date-picker-->
                <!--                            style="width:350px"-->
                <!--                            v-model="timers"-->
                <!--                            type="datetimerange"-->
                <!--                            @change="timeChange"-->
                <!--                            range-separator=""-->
                <!--                            start-placeholder=""-->
                <!--                            end-placeholder="">-->
                <!--                    </el-date-picker>-->
                <!--                </el-form-item>-->
                <el-form-item label="">
                    <el-button @click="search" type="primary" icon="Search"
                               v-show="isShowBtn('cvUploadManageCRUD','','Search')">
                    </el-button>
                </el-form-item>
                <el-form-item label="">
                    <el-button @click="handleEdit('','reset')" icon="Refresh"></el-button>
                </el-form-item>
                <el-form-item label="">
                    <el-button @click="addAdmin" type="primary" icon="Plus"
                               v-show="isShowBtn('cvUploadManageCRUD','','Create')">
                    </el-button>
                </el-form-item>
                <el-form-item label="">
                    <el-button @click="exportDataBackend" type="primary"></el-button>
                </el-form-item>
            </el-form>
        </div>

        <div class="table">
            <el-table :height="'calc('+(tableHeight)+'px)'" border :data="tableData" ref="tableref"
                      v-loading="loadingPage" style="width: 100%">
                <el-table-column type="index" width="60" align="center" label="">
                    <template #default="scope">
                        <span v-text="getIndex(scope.$index)"></span>
                    </template>
                </el-table-column>
                <el-table-column min-width="80" prop="avatar" label="">
                    <template #default="scope">
<!--                        <img  :src="scope.row.cv_img ? scope.row.cv_img : defaultImg" style="width: 30px;height: 30px" :onerror="defaultImg" >-->

                      <el-image :src="scope.row.cv_img ? scope.row.cv_img : defaultImg"
                                style="width: 50px;height: 75px"
                                :preview-src-list="scope.row.cv_list.split(',') ? scope.row.cv_list.split(',') : scope.row.cv_img"
                                preview-teleported>
                      </el-image>

                    </template>
                </el-table-column>
                <el-table-column min-width="110" prop="name" label=""></el-table-column>
                <el-table-column min-width="80" prop="age" label=""></el-table-column>
                <el-table-column min-width="100" prop="school" label=""></el-table-column>
                <el-table-column min-width="80" prop="xueli" label=""></el-table-column>
                <el-table-column min-width="150" prop="major" label=""></el-table-column>
                <el-table-column min-width="80" prop="nianxian" label=""></el-table-column>
                <el-table-column min-width="100" prop="nianxian" label=""></el-table-column>


                <el-table-column label="" fixed="right" width="180">
                    <template #header>
                        <div style="display: flex;justify-content: space-between;align-items: center;">
                            <div></div>
                            <div @click="setFull">
                                <el-tooltip content="" placement="bottom">
                                    <el-icon>
                                        <full-screen/>
                                    </el-icon>
                                </el-tooltip>
                            </div>
                        </div>
                    </template>
                    <template #default="scope">
                        <span class="table-operate-btn" @click="handleEdit(scope.row,'edit')"
                              v-show="isShowBtn('cvUploadManageCRUD','','Update')"></span>
                        <span class="table-operate-btn" @click="handleEdit(scope.row,'detail')"
                              v-show="isShowBtn('cvUploadManageCRUD','','Retrieve')"></span>
                        <span class="table-operate-btn" @click="handleEdit(scope.row,'delete')"
                              v-show="isShowBtn('cvUploadManageCRUD','','Delete')"></span>
                        <!--                        <span class="table-operate-btn" @click="handleEdit(scope.row,'disable')" v-show="isShowBtn('cvUploadManageCRUD','','Update')">-->
                        <!--                            <span v-if="scope.row.is_active"></span>-->
                        <!--                            <span v-else></span>-->
                        <!--                        </span>-->
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <Pagination v-bind:child-msg="pageparm" @callFather="callFather"></Pagination>
        <add-user ref="addUserFlag" @refreshData="getData"></add-user>
        <user-detail ref="userDetailFlag"></user-detail>
    </div>
</template>
<script>

    import addUser from "./components/addUserCV";
    import Pagination from "@/components/Pagination";
    import {dateFormats, getTableHeight} from "@/utils/util";
    import {CvUpload, CvUploadDelete, CvUploaddisableEdit, CvUploadExportexecl} from '@/api/api'
    import UserDetail from "./components/userCVDetail";

    export default {
        components: {
            UserDetail,
            Pagination,
            addUser
        },

        name: 'cvUploadManageCRUD',
        data() {
            return {
                isFull: false,
                tableHeight: 500,
                loadingPage: false,
                formInline: {
                    page: 1,
                    limit: 10,
                },
                defaultImg: require('../../assets/img/avatar.jpg'),
                pageparm: {
                    page: 1,
                    limit: 10,
                    total: 0
                },
                statusList: [
                    {id: 1, name: ''},
                    {id: 0, name: ''}
                ],
                identityList: [
                    {id: 0, name: ''},
                    {id: 1, name: ''},
                ],
                timers: [],
                tableData: [],

            }
        },

        methods: {
            // 
            getIndex($index) {
                // ( - 1) *  +  + 1
                return (this.pageparm.page - 1) * this.pageparm.limit + $index + 1
            },
            setFull() {
                this.isFull = !this.isFull
                window.dispatchEvent(new Event('resize'))
            },
            changeStatus(row) {
                // console.log(row,'row----')
            },
            addAdmin() {
                this.$refs.addUserFlag.addUserFn(null, '')
            },
            handleEdit(row, flag) {
                if (flag == 'edit') {
                    this.$refs.addUserFlag.addUserFn(row, '')
                } else if (flag == 'detail') {
                    this.$refs.userDetailFlag.addUserFn(row, '')
                } else if (flag == 'disable') {
                    let vm = this
                    CvUploaddisableEdit({id: row.id}).then(res => {
                        if (res.code == 2000) {
                            vm.$message.success(res.msg)
                            vm.search()
                        } else {
                            vm.$message.warning(res.msg)
                        }
                    })
                } else if (flag == 'delete') {
                    let vm = this
                    vm.$confirm('', {
                        closeOnClickModal: false
                    }).then(res => {
                        CvUploadDelete({id: row.id}).then(res => {
                            if (res.code == 2000) {
                                vm.$message.success(res.msg)
                                vm.search()
                            } else {
                                vm.$message.warning(res.msg)
                            }
                        })
                    }).catch(() => {

                    })
                } else if (flag == "reset") {
                    this.formInline = {
                        page: 1,
                        limit: 10
                    }
                    this.pageparm = {
                        page: 1,
                        limit: 10,
                        total: 0
                    }
                    this.getData()
                }
            },
            /**
             * URL
             */
            // 
            downloadFileURL(url) {
                var iframe = document.createElement("iframe")
                iframe.style.display = "none";
                iframe.src = url;
                document.body.appendChild(iframe);
            },
            exportDataBackend() {
                var params = {
                    page: 1,
                    limit: 9999,
                }
                CvUploadExportexecl(params).then(res => {
                    if (res.code == 2000) {
                        this.downloadFileURL(res.data.data)
                        //this.$message.warning(res.data.data)
                    }
                })
            },
            callFather(parm) {
                this.formInline.page = parm.page
                this.formInline.limit = parm.limit
                this.getData()
            },
            search() {
                this.formInline.page = 1
                this.formInline.limit = 10
                this.getData()
            },
            //
            async getData() {
                this.loadingPage = true
                CvUpload(this.formInline).then(res => {
                    this.loadingPage = false
                    if (res.code == 2000) {
                        this.tableData = res.data.data
                        this.pageparm.page = res.data.page;
                        this.pageparm.limit = res.data.limit;
                        this.pageparm.total = res.data.total;
                    }
                })
            },

            timeChange(val) {
                if (val) {
                    this.formInline.beginAt = dateFormats(val[0], 'yyyy-MM-dd hh:mm:ss');
                    this.formInline.endAt = dateFormats(val[1], 'yyyy-MM-dd hh:mm:ss');
                } else {
                    this.formInline.beginAt = null
                    this.formInline.endAt = null
                }
                this.search()
            },
            // 
            listenResize() {
                this.$nextTick(() => {
                    this.getTheTableHeight()
                })
            },
            getTheTableHeight() {
                let tabSelectHeight = this.$refs.tableSelect ? this.$refs.tableSelect.offsetHeight : 0
                tabSelectHeight = this.isFull ? tabSelectHeight - 110 : tabSelectHeight
                this.tableHeight = getTableHeight(tabSelectHeight)
            }

        },
        created() {
            this.getData()
        },
        mounted() {
            // 
            window.addEventListener('resize', this.listenResize);
            this.$nextTick(() => {
                this.getTheTableHeight()
            })
        },

        unmounted() {
            // 
            window.removeEventListener("resize", this.listenResize);
        },
    }
</script>
