<template>
    <div>
        <ly-crud ref="lycrud"  v-bind="crudConfig" >
            <template v-slot:customHandle="slotProps">
                <span class="table-operate-btn" @click="handleClick(slotProps.row,'disable')" v-show="hasPermission(this.$route.name,'Disable')">
                    <span v-if="slotProps.row.is_active"></span>
                    <span v-else></span>
                </span>
            </template>
        </ly-crud>
    </div>
</template>

<script lang="jsx">
    import {UsersUsers,UsersUsersAdd,UsersUsersDelete,UsersUsersEdit,UsersUsersdisableEdit,UsersUsersExportexecl} from '@/api/api'
    import LyCrud from "@/components/lycrud";
    import templateData from "@/components/dict/crudTemplateData"
    import { h,resolveComponent } from 'vue';
    export default {
        name: "userManageCrud",
        components: {LyCrud},
        data(){
            return{
                defaultImg:require('../../assets/img/avatar.jpg'),
                //crud
                crudConfig:{
                    //crud
                    crudRequest:{
                        add:UsersUsersAdd,
                        del:UsersUsersDelete,
                        edit:UsersUsersEdit,
                        search:UsersUsers,
                    },
                    //
                    searchBar:{
                        showSearchBar:true,//
                        searchColums:[
                            {label:'',type:'input',prop:'username',width:200,maxlength:60,placeholder:''},
                            {label:'',type:'input',prop:'mobile',width:200,maxlength:60,placeholder:''},
                            {label:'',type:'select',prop:'is_active',width:100,placeholder:'',
                                options:[
                                    {value:1,label:''},
                                    {value:0,label:''}
                                ]
                            },
                            {label:'',type:'datepicker-datetimerange',prop:'timers'},
                        ]
                    },
                    //
                    showPagination:true,
                    //
                    pageparams: {
                        limit: 10,//(10)//
                        //pageSizes:[10,20,30,40,50,200],//
                        // layout:'total, sizes, prev, pager, next, jumper',//
                    },
                    //crud
                    rowHandle:{
                        width: 180,//,0
                        fixed:"right",//
                        permission:{//
                            add:this.hasPermission(this.$options.name,'Create'),//bool this.$options.namenameuserManageCrud
                            del:this.hasPermission(this.$options.name,'Delete'),
                            edit:this.hasPermission(this.$options.name,'Update'),
                            search:this.hasPermission(this.$options.name,'Search'),
                            detail:this.hasPermission(this.$options.name,'Retrieve'),
                        }
                    },
                    //crud
                    formOptions: {
                        width:'45%',//dialog
                        gutter: 20, // Layout
                    },
                    //crud
                    tableOptions:{
                        border:true,
                        showHeader:true,
                    },
                    showSelectable:true,//
                    //table
                    tableColumns:[
                         // {label:'',type:'image',prop:'avatar',minWidth:'60',sortable: false,hidden:false,render:(row)=>{
                         //     let elImage = resolveComponent('el-image')//resolveComponentrender
                         //     return h(elImage,{
                         //          src:row.avatar ? row.avatar : this.defaultImg,
                         //          previewSrcList:[row.avatar ? row.avatar : this.defaultImg],//preview-src-listh-previewSrcList
                         //          previewTeleported:true,//body
                         //          style:"width: 30px;height: 30px",
                         //     },)
                         //  }
                         // },
                        {label:'ID',prop:'id',type:'input',minWidth:'100',sortable: false,hidden:true,
                            form:{
                                 //
                                 span:24,
                                 hidden:true,//
                             }
                        },
                        {label:'',type:'image-avatar',prop:'avatar',minWidth:'60',sortable: false,hidden:false,
                            render:(row)=>{
                                //jsx
                                return <el-image src={row.avatar ? row.avatar : this.defaultImg} style="width: 30px;height: 30px" preview-teleported={true} preview-srcList={[row.avatar ? row.avatar : this.defaultImg]}></el-image>
                            },
                            form:{
                                //
                                span:24,
                                // width:80,80px
                            }

                         },
                         {label:'',prop:'username',type:'input',minWidth:'110',sortable: false,hidden:false,
                            form:{
                                //
                                span:12,
                                rules: [{ required: true, message: '' }],
                                placeholder: '',
                            }
                         },
                         {label:'',prop:'nickname',type:'input',minWidth:'110',sortable: false,hidden:false,
                            form:{
                                //
                                span:12,
                                placeholder: '',
                            }
                         },
                        {label:'',prop:'password',type:'input',minWidth:'110',sortable: false,hidden:true,
                            form:{
                                //
                                span:12,
                                rules: [{ required: true, message: '' }],
                                placeholder: '',
                                showPassword:true,
                            }
                         },
                         {label:'',prop:'mobile',type:'input',minWidth:'100',sortable: false,hidden:false,
                            form:{
                                //
                                span:12,
                                rules: [{ required: true, message: '' }],
                                placeholder: '',
                            }
                         },
                         {label:'',prop:'is_active',type:'radio',minWidth:'100',sortable: false,hidden:false,
                             render: (row) =>{
                                let elTag = resolveComponent('el-tag')//resolveComponentrender
                                if(!row.is_active){
                                    return h(elTag, {
                                      type:'warning'
                                    },()=>'');//()=>console
                                }else {
                                    return h(elTag, {
                                      type:'success'
                                    },()=>'');
                                }
                              },
                             form:{
                                 //
                                 span:24,
                                 options:templateData.STATUS_BOOL,//radio
                                 defaultValue:true,//
                                 // valueChange(e){//
                                 //     console.log(e,'---')
                                 // },
                             }
                         },
                         {label:'',prop:'create_datetime',minWidth:'150',sortable: false,hidden:false},
                    ],
                },

            }
        },
        methods:{
            setFull(){
                this.$refs.lycrud.setFull()
            },
            //
            handleClick(row,flag){
                let vm = this
                if(flag=='disable'){
                    UsersUsersdisableEdit({id:row.id}).then(res=>{
                        if(res.code == 2000) {
                            vm.$message.success(res.msg)
                            vm.$refs.lycrud.handleRefresh()//
                        } else {
                            vm.$message.warning(res.msg)
                        }
                    })
                }
            },
        },
    }
</script>

<style scoped>

</style>